.button {
  padding: 15px 0 14px;
  text-align: center;
  font-weight: 700;
  color: $header-text-color;
  background-color: $main-red-color;
  transition: background-color $effectDuration;

  &:visited {
    background-color: #eb343d;
  }

  &:hover {
    background-color: #ae1c23;
    cursor: pointer;
  }
}
